.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 50px;
  height: 100vh;
}

* {
  box-sizing: border-box;
}

.list {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.list-item-wrapper {
  padding: 0px 20px;
  transition: transform 200ms ease 0s;
}

.list-item {
  border: 1px solid rgb(51, 48, 48);
  border-radius: 4px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 300px;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: stretch;
  height: 500px;
  padding: 20px;
  background-color: #fff;
}

.list-item:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.win .list-item {
  border: 1px solid rgb(37, 194, 16);
}

.list-item-head {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

h3 {
  font-size: 30px;
}

.list-item-img {
  height: 250px;
  overflow: hidden;
}
.list-item-img img {
  width: 100%;
}

.list-item-desc {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 15px;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.highlight {
  background-color: #ffff00;
}

*[draggable] {
  cursor: grab;
}

*[draggable]:active {
  cursor: grabbing;
}

.dragging {
  border: 1px solid #a8a1a1;
  background-color: #a8a1a1;
  cursor: grabbing;
  color: #a8a1a1;
}

.dragging img {
  display: none;
}

